import React from 'react';
import './signup.css';

class Signup extends React.Component {

    render() {
        return (
            <div className="signfirst" >
                <div className="signupsecond">Linked<i class="fab fa-linkedin"></i></div>
                <span className="signspan">Make the most of your professional life</span>
                <table className="signtable" id="#signtb">
                    <tr>
                        <td>Firstname</td>
                    </tr>
                    <tr>
                        <td><input id="signtr" className="text" type="text" /></td>
                    </tr>
                    <tr>
                        <td >Secondname</td>
                    </tr>
                    <tr>
                        <td><input id="signtr" className="text" type="text" /></td>
                    </tr>
                    <tr>
                        <td >Email-id</td>
                    </tr>
                    <tr>
                        <td><input id="signtr" className="text" type="text" /></td>
                    </tr>
                    <tr>
                        <td>Password</td>
                    </tr>
                    <tr>
                        <td><input id="signtr" className="text" type="password" /></td>
                    </tr>
                    <tr>
                        <td><button className="signbut">Join Now</button></td>
                    </tr>
                    <tr>
                        <td className="signor">-----------or-----------</td>
                    </tr>
                    <tr>
                        <td><button className="signface"><i class="fab fa-facebook-square" id="signface1"></i>Continue with Facebook</button></td>
                    </tr>
                </table>
            </div>
        )
    }

}

export default Signup;

