import React from 'react';
import axios from 'axios';
class Axios extends React.Component {
    getdata(){
        axios.get('https://jsonplaceholder.typicode.com/comments').then(
            (data)=>{
                console.log(data)
            }
        )
    }
    render() {
        return (
            <div>
              <button onClick={()=>this.getdata()}>view</button>   

            </div>
            )
        }
    
    }
    
    export default Axios;   