import React from 'react';
import { Link } from 'react-router-dom';
// import './login.css';
class Header extends React.Component {

    render() {
        return (
            <div>
                <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
                    <ul className="navbar-nav mr-auto">
                        <li className="nav-item">
                            <Link className="nav-link" to={'/login'}><i class="fas fa-clipboard-check"></i> Login</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to={'/signup'}><i className="fas fa-sign-in-alt"></i> sign up</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to={'/employer'}><i className="far fa-user"></i> employer</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to={'/calculator'}><i className="fas fa-calculator"></i> calculator</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to={'/empdetails'}><i className="fab fa-angellist"></i> empdetails</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to={'/navbar'}><i className="fas fa-compass"></i> navbar</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to={'/page'}><i className="fas fa-pager"></i> page</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to={'/Error'}><i className="fas fa-bomb"></i> Error</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to={'/reactchart'}><i className="fas fa-bomb"></i> Reactchart</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to={'/axios'}><i className="fas fa-bomb"></i> Axios</Link>
                        </li>
                    </ul>
                </nav>
            </div>
        )
    }
}

export default Header;   