import React from 'react';
import './calculator.css';

class Calculator extends React.Component {
    render() {
        return (
            <div >
                <table className="caltable">
                    <tr>
                        {/* <td colspan="4" ><button className="cal1table">0</button></td> */}
                        <td colspan="4" ><textarea className="cal1table">0</textarea></td>
                    </tr>
                    <tr>
                        <td><button className="buttable">Mc</button></td>
                        <td><button className="buttable">M+</button></td>
                        <td><button className="buttable">M-</button></td>
                        <td><button className="buttable">Mr</button></td></tr>
                    <tr>
                        <td><button className="buttable">C</button></td>
                        <td><button className="buttable">-</button></td>
                        <td><button className="buttable">*</button></td>
                        <td><button className="buttable">Backspace</button></td></tr>

                    <tr>
                        <td><button className="buttable">1</button></td>
                        <td><button className="buttable">2</button></td>
                        <td><button className="buttable">3</button></td>
                        <td><button className="buttable">4</button></td></tr>
                    <tr>
                        <td><button className="buttable">5</button></td>
                        <td><button className="buttable">6</button></td>
                        <td><button className="buttable">7</button></td>
                        <td><button className="buttable">8</button></td></tr>
                    <tr>
                        <td><button className="buttable">9</button></td>
                        <td><button className="buttable">divide</button></td>
                        <td><button className="buttable">+</button></td>
                        <td><button className="buttable">=</button></td></tr>
                    <tr>
                        <td><button className="buttable">.</button></td>
                        <td><button className="buttable">0</button></td>
                        <td><button className="buttable">%</button></td>
                        <td><button className="buttable">0</button></td></tr>
                </table>
            </div>




        )
    }

}

export default Calculator;