
import React from 'react';
import './page.css';
import axios from 'axios';


class Page extends React.Component {
  data=[];
    //pageButtons = [1, 2, 3, 4,5];
    pageButtons = [10, 11];
    lastpage = 49;
    //lastpage=20;
    getdata() {
        axios.get('https://jsonplaceholder.typicode.com/comments').then(
            (res) => {
                console.log(res);
                this.data=res.data;
                this.setState({reload:true});

            
            }
        )
    }

    state = {

        page: 0,
        // end: 30,
        pagelimit: 10
    }



    setpage(page) {
        this.setState({ page: page })

    }
    previous() {
        // this.setState({ initial: this.state.initial - 10, end: this.state.end -10  })
        this.setpage(this.state.page - 1)
    }
    after() {
        this.setpage(this.state.page + 1)

        // this.setState({ initial:this.state.initial -10, end: this.state.end - 90 })
    }

    generatepagebuttons() {
        this.pageButtons = [];
        let buttonStart = this.state.page - 1 >= 1 ? this.state.page - 1 : 1;
        let buttonend = this.state.page + 3 <= this.lastpage ? this.state.page : this.lastpage;


        for (let i = buttonStart; i <= buttonend; i++) {
            this.pageButtons.push(i)
        }
    }
    search(event) {
        // alert('hi');
        console.log(event);
        console.log(event.target);
        console.log(event.target.value);
        let newPageLimit = parseInt(event.target.value);
        this.setState({ pagelimit: newPageLimit, page: 0 });
    }
    render() {
        this.generatepagebuttons();
        let showData = this.data.slice(this.state.page * this.state.pagelimit, (this.state.page + 1) * this.state.pagelimit);
        let displayData = showData.map(each => {
            return (

                <tr>
                    <td className="pagetd">{each.id}</td>
                    <td className="pagetd">{each.name}</td>
                    <td className="pagetd">{each.postId}</td>
                    <td className="pagetd">{each.email}</td>
                    <td className="pagetd">{each.body}</td>
                </tr>



            );
        });
        let displayButtons = this.pageButtons.map(each => {
            return <button onClick={() => this.setState({ page: each - 1 })}>{each}</button>
        })
        return (
            <div>
                <div>
                    <i className="far fa-angry"></i>





                    <input type="search" id="text" placeholder="texthere" className="input" />
                    <button type="submit" className="button"><i className="fa fa-search"></i></button>


                    <select className="select" onChange={(event) => this.search(event)}>
                        <option value="5" >5</option>
                        <option value="10">10</option>
                        <option value="15">15</option>
                        <option value="20">20</option>
                    </select>
                    <button className="input">&lt;&lt;</button>
                    <button onClick={() => this.previous()} className="but">&lt;</button>
                    {/* <button onClick={() => this.first()} className="but">1</button> */}
                    {/* <button>2</button>
                    <button>3</button>
                    <button>4</button>
                    <button>5</button> */}
                    {displayButtons}
                    <button onClick={() => this.after()} className="but">&gt;</button>
                    <button className="but">&gt;&gt;</button>

                    <button onClick={() => this.getdata()}>view</button>



                </div>
                <table className="pagetable" >


                    <tr className="pagetr">
                        <th className="pagetd">POST</th>
                        <th className="pagetd">id</th>
                        <th className="pagetd">name</th>
                        <th className="pagetd">Email</th>
                        <th className="pagetd">Body</th>
                    </tr>
                    {displayData}
                </table>
            </div >)
    }

}

export default Page;   