import React from 'react';
import './employer.css';
import { Link } from 'react-router-dom';
import { EmpConsumer } from './EmpProviders';
class Empdetails extends React.Component {

    render() {
        return (
            <div className="emptotaldiv">

                <table className="empdetails">
                    <EmpConsumer>{({employee}) => (
                        <tbody>
                            <tr>
                                <th className="empdetailstd">Employee</th>
                                <th className="empdetailstd"> Details</th>
                            </tr>
                    <tr><td className="empdetailstd">Name</td><td className="empdetailstd">{employee.Name}</td></tr>
                            <tr><td className="empdetailstd">Age</td><td className="empdetailstd">{employee.Age}</td></tr>
                            <tr><td className="empdetailstd">Email</td><td className="empdetailstd">{employee.Email}</td></tr>
                            <tr><td className="empdetailstd">Date of joining</td><td className="empdetailstd">10/06/2019</td></tr>
                            <tr><td className="empdetailstd">Phone number</td><td className="empdetailstd">1628101902299</td></tr>
                            <tr><td className="empdetailstd">Street</td><td className="empdetailstd">{employee.Street}</td></tr>
                            <tr><td className="empdetailstd"> City</td><td className="empdetailstd">{employee.City}</td></tr>
                            <tr><td className="empdetailstd"> Zip</td><td>{employee.Zip}</td></tr>
                            <tr><td className="empdetailstd">Region</td><td className="empdetailstd">{employee.Region}</td></tr>
                            <tr><td className="empdetailstd">Country</td><td className="empdetailstd">{employee.Region}</td></tr>
                            <tr><td className="empdetailstd">Info</td><td className="empdetailstd">{employee.Info}</td></tr>
                            <tr><td className="empdetailstd">EmployeeID </td><td className="empdetailstd">{employee.EmployeeID}</td></tr>
                        </tbody>
                    )}</EmpConsumer>
                </table>

                {/* <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
                    <ul className="navbar-nav mr-auto">

            <li className="nav-item">
                        <Link className="nav-link" to={'/employer'}><i className="far fa-user"></i>Back</Link>
                        </li>
                        </ul>
                </nav> */}
                <div id="emplink">
                    <Link to={'/employer'}>
                        <button id="empbutton"><i class="fas fa-backward" ></i></button>
                    </Link>


                </div>

            </div>

        )
    }

}

export default Empdetails;
